@tailwind base;
@tailwind components;
@tailwind utilities;

.mobileNav {
    @apply fixed top-10 left-0 w-full h-1/4 bg-slate-600
}
.mobileNav li {
    @apply w-full h-full text-center border-b-2  
}

.projectDisc{
    @apply absolute left-0 right-0 bottom-0 top-0 bg-purple-900  text-center p-5 rounded opacity-0 duration-500 hover:opacity-100
}

.btn{
    @apply border-2 border-gray-400 bg-purple-500 rounded p-1 font-bold 
}
html{
    scroll-behavior: smooth;}